import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate, useLocation } from "react-router-dom";
import { TweenMax, Linear } from "gsap/gsap-core";
import gsap from 'gsap';
import AOS from "aos";
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import Navigation from "./Navigation";

function Header(){
	let navigate=useNavigate();
	let location=useLocation();
	let locationData=location.state;
	console.log(locationData);


	let data=useSelector(state => state.navigation);

	useEffect (() => {
		gsap.registerPlugin(ScrollToPlugin);

		let $text=document.querySelector('.track .track_txt');
		let $wrap=document.querySelector('.track');

		let $textClone;
		$textClone=$text.cloneNode(true);
		$wrap.appendChild($textClone);

		TweenMax.to($wrap, 40, {
			x: -($text.offsetWidth+1),
			ease: Linear.easeNone,
			repeat: -1 //  
		});

		let $text2=document.querySelector('.track2 .track_txt');
		let $wrap2=document.querySelector('.track2');

		let $textClone2;
		$textClone2=$text2.cloneNode(true);
		$wrap2.appendChild($textClone2);
		
		TweenMax.to($wrap2, 30, {
			x: +($text2.offsetWidth-1),
			ease: Linear.easeNone,
			repeat: -1 //  
		}); 

		let $text3=document.querySelector('.track3 .track_txt');
		let $wrap3=document.querySelector('.track3');

		let $textClone3;
		$textClone3=$text3.cloneNode(true);
		$wrap3.appendChild($textClone3);
	
		TweenMax.to($wrap3, 40, {
			x: -($text3.offsetWidth+1),
			ease: Linear.easeNone,
			repeat: -1 //  
		});

		let n=0;
		let t=0;
		let prevN;
		let total;
		let winHalf;

		let pageList=[];
	
		pageList[0]=document.getElementById("main");
	
		let section=document.querySelectorAll("section");

		for(let i=0; i<section.length; i++){
			pageList.push(section[i]);
		}

		// console.log("1", targety);
		if (locationData !== undefined && locationData !== null) {
			let targety = pageList[locationData].offsetTop;
			gsap.to(window, { scrollTo: targety, duration: 0.5 });
		}
		
		let gnb=document.querySelector(".sidebar");
		let gnbList=gnb.firstElementChild.children;	
		let nav=document.getElementById("gnb");
		let navLi=nav.firstElementChild.children;
		let mobile=document.getElementById("mobile");
		let mobileLi=mobile.firstElementChild.children;

		total=pageList.length;

		function scrollListener(){
			t=window.scrollY;
	
			if(t < pageList[1].offsetTop){
				n=0;
			}
			else if(t < pageList[2].offsetTop){
				n=1;
			}
			else if(t < pageList[3].offsetTop){
				n=2;
			}
			else if(t < pageList[4].offsetTop){
				n=3;
	
				if(window.innerHeight+t+5 > document.body.scrollHeight){
					n=4;
				}
			}
			else{
				n=4;
			}

			for(let i=0; i<gnbList.length; i++){
				if(i == n){
					if(gnbList[i].classList.contains("active") == false){
						gnbList[i].classList.add("active");
						navLi[i].classList.add("active");
						mobileLi[i].classList.add("active");
					}
				}
				else{
					if(gnbList[i].classList.contains("active") == true){
						gnbList[i].classList.remove("active");
						navLi[i].classList.remove("active");
						mobileLi[i].classList.remove("active");
					}
				}
			}
		}

		window.addEventListener("scroll", scrollListener);

		for(let i=0; i<total; i++){
			gnbList[i].addEventListener("click", e => {
				e.preventDefault();

				n=i;

				let targety=pageList[n].offsetTop;
	
				gsap.to(window, {scrollTo: targety, duration: 0.5});
			});

			navLi[i].addEventListener("click", e => {
				e.preventDefault();

				n=i;

				let targety=pageList[n].offsetTop;

				gsap.to(window, {scrollTo: targety, duration: 0.5});
			});
		}

		for(let i=0; i<total; i++){
			mobileLi[i].addEventListener("click", e => {
				e.preventDefault();
				n=i;
				let tabFlag;
				let targety=pageList[n].offsetTop;

				// console.log(pageList[n]);
				tabFlag=false;
				tab.classList.remove("active");
				dim.classList.remove("active");
				gsap.to(window, {scrollTo: targety, duration: 0.5});
				gsap.fromTo(dim, {display: "none", opacity: 0}, {opacity: 1, duration: 0.3}); 
				gsap.fromTo(mobile, {display: "none", opacity: 0}, {opacity: 1, duration: 0.5}); 
			});
		}

		let w;

		window.addEventListener("resize", function(){
			w=window.innerWidth;

			if(w > 720){
				if(mobile.classList.contains("active")){
					mobile.classList.remove("active");
					tab.classList.remove("active");
					dim.classList.remove("active");
					gsap.fromTo(mobile, {display: "none", opacity: 0}, {opacity: 1, duration: 0.5}); 
					gsap.fromTo(dim, {display: "none", opacity: 0}, {opacity: 1, duration: 0.3}); 
				}
			}
		});

		// tab
			
		let tab=document.querySelector("header .tab");
		let dim=document.querySelector("header .dim");
		let tabFlag=false;
		
		tab.addEventListener("click", function(e){
			e.preventDefault();
			
			if(tab.classList.contains("active") === false){
				tabFlag=true;
				tab.classList.add("active");
				dim.classList.add("active");
				mobile.classList.add("active");
				gsap.fromTo(mobile, {display: "block", opacity: 0}, {opacity: 1, duration: 0.3}); 
				gsap.fromTo(dim, {display: "block", opacity: 0}, {opacity: 1, duration: 0.3}); 
			}
			else{
				tabFlag=false;
				tab.classList.remove("active");
				dim.classList.remove("active");
				mobile.classList.remove("active");
				gsap.fromTo(mobile, {display: "none", opacity: 0}, {opacity: 1, duration: 0.5}); 
				gsap.fromTo(dim, {display: "none", opacity: 0}, {opacity: 1, duration: 0.3}); 
			}
		});

		AOS.init({
			easing: "ease-in-out-sine",
			once: true
		});

		return(() => {
			window.removeEventListener("scroll", scrollListener);
		});
	}, []);

	return(
		<header>
			<div className="logo"><h1><a href="">HMJ</a></h1></div>			
			<nav id="gnb">
				<Navigation propsValue={data}/>
			</nav>

				<nav id="mobile">
					<Navigation propsValue={data}/>
				</nav>

			<div className="tab">
				<span></span>
				<span></span>
				<span></span>
			</div>
			
			<div className="dim"></div>
	</header>
	);
};

export default Header;