import AOS from "aos";
import 'animate.css';
import 'aos/dist/aos.css';
import { useSelector } from 'react-redux';

import Section1Title from "./Section1Title";
import Section1Txt from "./Section1Txt";


function Section1Keytext(){
	let data=useSelector(state => state.section1Keytxt);
	let {title, txt}=data;
	
	return(
		<div className="keytxt" >
			<Section1Title propsValue={data.title} />
			<Section1Txt propsValue={data.txt}/>
		</div>
	);
};

export default Section1Keytext;