import { useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";

import Section3Track from "./Section3Track";

function Section3(){
	let navigate=useNavigate();
	let location=useLocation();
	let link=location.state;
	

	return(
		<section id="sec3"> 
			<Section3Track />
			<div className="button">
			<button onClick={() => navigate("/subpage",  {state: "projects"})}>CLICK
			<img src={process.env.PUBLIC_URL+"/assets/images/arrow.png"} alt="arrow" />
			</button>
	
			</div>
		</section>
	);
};

export default Section3;