import { useSelector } from 'react-redux';

function Section4(){
	let snsData = useSelector(state => state.sns);
	let emailData = useSelector(state => state.email);
	let link = useSelector(state => state.link);
	// console.log(link);
	return(
		<section id="sec4">
			<div className="inner">
				<div className="top">
					<div className="top_left"  data-aos="fade-right"  data-aos-duration="1200">
						<p>
							Let's craft your web experience together.
						</p>
					</div>
					<div className="top_right" data-aos="fade-left"  data-aos-duration="1200">
						<ul>
							{/* <li><a href="">KAKAOTALK</a></li>
							<li><a href="">INSTAGRAM</a></li>
							<li><a href="">EMAIL</a></li> */}
							{snsData.map((d,i)=><li key={i}><a href={link[i]}>{d}</a></li>)}
						</ul>
					</div>
				</div>
				<div className="bottom"data-aos="fade-right"  data-aos-duration="1500">
					<div className="email">
						<a href="">{emailData}</a>
					</div>
				</div>
			</div>
		</section>
	);
};

export default Section4;