import Header from "./Header";
import Side from "./Side";
import Visual from "./Visual";
import Track from "./Track";
import { useSelector } from "react-redux";

function Main(){
	let data=useSelector(state => state.visual);
	
	return(
		<div id="main">	
			<Header />
			<Side />
			<Visual propsValue={data}/>
			<Track />
		</div>
	);
};

export default Main;