function Sub3CaseStudy(props){
	let data=props.propsValue;

	return(
		<div className="casestudy" data-aos="fade-down"  data-aos-duration="2200">
			<ul>
				{
					data.map((d, i) => {
						let {date, text}=d;

						return(
							<li key={i}>
								<a href= {d.link} target="pop">
									<div className="text">
										<span>{text.span}</span>
									</div>
									<div className="date">{date}</div>
								</a>
							</li>
						);
					})
				}
				{/*
				<li>
					<a href="">
						<div className="text">
							<span>MISHA   </span>
						</div>
						<div className="date">
							2023.12.33
						</div>
					</a>
				</li>
				<li>
					<a href="">
						<div className="text">
							<span>MISHA   </span>
						</div>
						<div className="date">
							2023.12.33
						</div>
					</a>
				</li>
				<li>
					<a href="">
						<div className="text">
							<span>MISHA   </span>
						</div>
						<div className="date">
							2023.12.33
						</div>
					</a>
				</li>
				*/}
		
			</ul>
		</div>
	);
};
export default Sub3CaseStudy;