import Skill from "./Skill";
import { useSelector } from "react-redux";

function Section2(){
	let data=useSelector(state => state.skillSwiper);
	let {skillSwiper}=data;
	return(
		<section id="sec2">
			<div className="title" data-aos="fade-up"  data-aos-duration="1000">
				<strong>SKILLS</strong>
			</div>
			<div className="inner"data-aos="fade-up"  data-aos-duration="1500" >	
			<Skill propsValue={data}/>
			</div>
		</section>
	);
};

export default Section2;