import Sub1Works from "./Sub1Works";
import { useSelector } from "react-redux";

function Sub1(){
	let data=useSelector(state => state.works);
	return(
		<section id="sub1" >
		<div className="title" data-aos="fade-right"  data-aos-duration="1700"> 
			<h2>PROJECT</h2>
			<p>
				"I am ready to collaborate with businesses in need of adaptable developers."
			</p>	
		</div>

		<Sub1Works propsValue={data} />
	</section>
	);
};

export default Sub1;

