import React, { useRef, useState } from 'react'; // Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react'; // Import Swiper styles
import { useSelector } from 'react-redux';
import { Scrollbar } from 'swiper/modules';


import 'swiper/css';
import 'swiper/css/scrollbar';


import Sub2sourceList from './Sub2sourceList';

function Sub2source(props){
	let data=useSelector(state => state.opensourceSwiper);

	return(
		<Swiper
			slidesPerView={1}
			spaceBetween={20}
			loop={true}
			breakpoints={{
				640: {
					slidesPerView: 1
				},
				768: {
					slidesPerView: 2
				},
				1024: {
					slidesPerView: 3.5
				}
			}}

			scrollbar={{
				hide: true,
				}}
				modules={[Scrollbar]}
				className="mySwiper"
	>
			{/* <SwiperSlide>
				<Sub2sourceList propsValue={data}/>
			</SwiperSlide>

			<SwiperSlide>
				<Sub2sourceList propsValue={data} />
			</SwiperSlide>

			<SwiperSlide>
				<Sub2sourceList propsValue={data} />
			</SwiperSlide>

			<SwiperSlide>
				<Sub2sourceList  propsValue={data}/>
			</SwiperSlide>

			<SwiperSlide>
				<Sub2sourceList  propsValue={data}/>
			</SwiperSlide>

			<SwiperSlide>
				<Sub2sourceList  propsValue={data}/>
			</SwiperSlide>*/}
			{data.map((d, i) => 
			<SwiperSlide key={i}><Sub2sourceList propsValue={d} /></SwiperSlide>)}
		</Swiper>
	);
};

export default Sub2source;