
function SkillList(props){
	let data=props.propsValue;

	return(
		<>
		<div className="sub_title">
		{data.category}
		</div>
		<div className="img">
			<ul>
				{/* <li><img src={"./assets/images/" +icon.title}alt={icon.alt} /></li> */}
				{/* <li><img src={"./assets/images/"+path } alt={alt} /></li>
				<li><img src={"./assets/images/"+path} alt={alt} /></li>
				<li><img src={"./assets/images/"+path} alt={alt} /></li> */}
				{data.icon.map((d, i) => <li key={i}><img src={process.env.PUBLIC_URL+"/assets/images/" +d.title}alt={d.alt} /></li>)}
			</ul>
		</div>
		<p>
			{data.desc}
		</p>
		</>
	);
};

export default SkillList;		