import Sub3CaseStudy from "./Sub3CaseStudy";
import { useSelector } from "react-redux";
import data from "../data";

function Sub3(){
	let data=useSelector(state => state.caseStudy);
	return(
		<div id="sub3">
		<div className="title" data-aos="fade-down"  data-aos-duration="1200">
			<strong>CASE STUDY</strong>
		</div>
		<Sub3CaseStudy propsValue={data}/>
	</div>
	);
};

export default Sub3;