import React, { useRef, useState } from 'react'; // Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react'; // Import Swiper styles
import { Scrollbar } from 'swiper/modules';
import { useSelector } from 'react-redux';

import 'swiper/css';
import 'swiper/css/scrollbar';

import SkillList from './SkillList';

function Skill(props){
	let data=useSelector(state => state.skillSwiper);

	let sliderHeight={
		height: 350
	};
	return(	
		<Swiper
			slidesPerView={1}
			spaceBetween={40}
			loop={true}
			breakpoints={{
				520: {
					slidesPerView: 1.5
				},
				768: {
					slidesPerView: 2.5
				},
				1024: {
					slidesPerView: 3
				}
			}}

			scrollbar={{
				// hide: true,
				}}
				modules={[Scrollbar]}
				className="mySwiper"
			style={sliderHeight}
		>
			{/* <SwiperSlide>
				<SkillList propsValue={data[0]} />
			</SwiperSlide>


			<SwiperSlide>
				<SkillList propsValue={data[1]} />
			</SwiperSlide>


			<SwiperSlide>
				<SkillList propsValue={data[2]} />	
			</SwiperSlide>


			<SwiperSlide>
				<SkillList propsValue={data[3]} />
			</SwiperSlide>


			<SwiperSlide>
				<SkillList propsValue={data[4]} />
			</SwiperSlide>


			<SwiperSlide><SkillList propsValue={data[5]} /></SwiperSlide> */}
				{data.map((d,i) => <SwiperSlide key={i}><SkillList propsValue={d} /></SwiperSlide>)}
		</Swiper>
	);
};

export default Skill;