import AOS from "aos";
import 'animate.css';
import 'aos/dist/aos.css';
import { useSelector } from 'react-redux';

function Visual(props){
	let data=useSelector(state => state.visual);
	let {strong, span, keytext}=data;
	
	return(
	<div className="visual" >
		<div className="title" >
			<strong  data-aos="fade-up"  data-aos-duration="1000">{strong}</strong>
			<span  data-aos="fade-up"  data-aos-duration="1500">{span}</span>
			<span className="line"></span>
		</div>
		<div className="keytxt" data-aos="fade-down" data-aos-duration="2500">
		<p>{keytext.p1}</p>
		<strong>{keytext.strong}</strong>
		<p>{keytext.p2}</p>
	</div>
	</div>
	);
};

export default Visual;