function Sub2sourceList(props){
	let data=props.propsValue;
	let {img, h3, title, p, link, button}=data;

	return(
		<>
			<div className="img"><a href={button.web} target="pop"><img src={process.env.PUBLIC_URL+"/assets/images/"+img.path} alt={img.path} /></a></div>
			<div className="con">
				<h3>{h3}</h3>
				<ul>
					<li>
						<span className="bold">{title.bold}</span>
						<span>{title.span}</span>
					</li>	
					<li>
					<p>{p}</p>
					</li>
					<li className="button">
						<a href={button.web} target="pop">WEB</a>
						{
							(button.git) ? <a href={button.git} className="git" target="pop">GIT</a> : null
						}
					</li>
				</ul>
			</div>
		</>
	);
};

export default Sub2sourceList;