import React, { useRef, useState } from 'react'; // Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react'; // Import Swiper styles
import { Scrollbar } from 'swiper/modules';
import { useSelector } from 'react-redux';

import 'swiper/css';
import 'swiper/css/scrollbar';

import Sub2source from "./Sub2source";

function Sub2(){
	let data=useSelector(state => state.opensourceSwiper)

	let sliderHeight={
		height: 300
	};
	return(
	<section id="sub2">
		<div className="title" data-aos="fade-up"  data-aos-duration="1200">
			<strong>OPREN SOURCE</strong>
		</div>
		<div className="inner" data-aos="fade-up"  data-aos-duration="2000">
			<Sub2source propsValue={data}/>	
		</div>
	</section>
	);
};

export default Sub2;